.Navigation {
  width: 100%;
  height: 0px;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 1s;
  .menu {
    height: 100%;
  }
  a {
    text-decoration: none;
    color: #ffffff;
  }
}

.open {
  height: 20vh;
  font-size: 35px;
  font-weight: 600;
  line-height: 20vh;
}

@media (max-width: 766px) {
  .Navigation div {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    z-index: 899;
    a {
    }
  }
  .open {
    height: 100vh;
    font-size: 30px;
    line-height: 20vh;
  }
}
@media (min-width: 767px) {
  .Navigation {
    .menu {
      width: calc(100% - 170px);
      display: flex;
      justify-content: space-around;
    }
    a {
    }
  }
  .open {
    height: 20vh;
    font-size: 30px;
    line-height: 20vh;
  }
}

.closed {
  height: 0;
  line-height: 0px;

  font-size: 0;
}
